<template>
	<view class="content">
		<customHeader :title="pageName" :showBack="false" :headHeight="headerHeight"></customHeader>
		<view class="cont" :style="{marginTop:marginTop+'px'}">
			<view class="top_area" :style="{top:marginTop+'px'}">
				<!-- 顶部账户信息 -->
				<view class="account" :style="{height:commiCardHeight+'px'}">
					<image :src="imgUrl+'/broker/commission_bg.png'" :style="{height:commiCardHeight+'px',width:'100%'}"></image>
					<view class="top_line flex flex_btween">
						<view class="flex flex-start">
							<image :src="imgUrl+'/broker/ic_finance.png'" mode="widthFix"></image>
							<text>佣金结余</text>
						</view>
						<view class="flex flex-start">
							<view class="bold">{{info.balance}}</view>
							<view class="text">元</view>
						</view>
					</view>
					<view class="bot_line flex flex_btween">
						<view class="bot_box">
							<view class="tit">佣金入账</view>
							<view class="text"><text class="yellow">{{info.income}}</text>元</view>
						</view>
						<view class="bot_box">
							<view class="tit">佣金支出</view>
							<view class="text"><text class="yellow">{{info.expenditure}}</text>元</view>
						</view>
					</view>
				</view>
				<!-- 筛选按钮 -->
				<view class="sele_box flex flex_end" style="height:20px;line-height: 20px;float: right;" :style="{marginBottom:tabMargin+'px'}" @click="openMask">
					<view class="text">筛选</view>
					<image :src="imgUrl+'/broker/ic_trangle_down.png'" mode="widthFix"></image>
				</view>
			</view>
			<view class="box" :style="{top:boxTop+'px',background:list.length==0?'#fff':'#F3F3F5'}">
				<scroll-view :scroll-top="scrollTop" scroll-y="true" class="scroll-Y" :style="{height:scrollHeight+'px'}" refresher-enabled="true" :refresher-triggered="refreshStatus" @refresherpulling="refreshData"
					@scrolltolower="lower">
					<view class="item" v-for="(item,index) in list" :key="index" @click="toDetail(item.id)">
						<view class="top flex flex_btween">
							<view class="title">{{item.project_name}}</view>
						</view>
						<view class="middle flex flex_btween">
							<view class="left flex flex-start">
								<view class="tit">项目收入：</view>
								<view class="count flex flex-start"><text class="mark bold">{{item.income}}</text>元</view>
							</view>
							<view class="right flex flex-start">
								<view class="tit">项目支出：</view>
								<view class="count flex flex-start"><text class="mark yellow bold">{{item.expenditure}}</text>元</view>
							</view>
						</view>
					</view>
					<view class="empty" v-if="list.length == 0">
						<image :src="imgUrl+'/broker/empty.png'" mode="heightFix"></image>
					</view>
				</scroll-view>
			</view>
		</view>
		<!-- 筛选 -->
		<view class="mask" v-show="showMask">
			<view class="inner" :style="{paddingTop:marginTop+tabMargin+'px'}">
				<view class="title flex flex_btween">
					<view class="text">项目筛选</view>
					<image :src="imgUrl+'/broker/ic_close_circle.png'" mode="widthFix" @click="close"></image>
				</view>
				<view class="sele_line">
					<view class="tit">公司名称</view>
					<view class="sele_area">
						 <uni-data-select
						      v-model="comValue"
						      :localdata="comRange"
						      @change="changeCom"
						    ></uni-data-select>
							<image :src="imgUrl+'/broker/ic_trangle_down.png'" mode="widthFix"></image>
					</view>
				</view>
				<view class="sele_line">
					<view class="tit">项目状态</view>
					<view class="sele_area">
						 <view class="checkbox flex flex_btween">
							 <view class="check flex flex-start" v-for="(item,index) in tabs" :key="index" @click="changeStatus(item)">
								 <image :src="currentStatus==item.value?imgUrl+'/broker/ic_check_sele.png':imgUrl+'/broker/ic_check_normal.png'" mode="widthFix"></image>
								 <view class="text">{{item.text}}</view>
							 </view>
						 </view>
					</view>
				</view>
				<view class="sele_line">
					<view class="tit">接单时间</view>
					<view class="sele_area">
						<view class="time">
							<uni-datetime-picker
								type="date"
								v-model="startTime"
								@change="changeStartTime"
							/>
							<image :src="imgUrl+'/broker/ic_trangle_down.png'" mode="widthFix"></image>
						</view>
						<view class="link">至</view>
						<view class="time">
							<uni-datetime-picker
								type="date"
								v-model="endTime"
								@change="changeEndTime"
							/>
							<image :src="imgUrl+'/broker/ic_trangle_down.png'" mode="widthFix"></image>
						</view>
					</view>
				</view>
				<view class="btn_wrap flex flex_btween">
					<view class="btn" @click="clear" style="background: transparent;color:#F3A005;border:1px solid  #F3A005;box-sizing: border-box;" :style="{height:subTabHeight-2+'px',lineHeight:subTabHeight-2+'px'}">清除</view>
					<view class="btn" @click="searchList" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">筛选</view>
				</view>
			</view>
		</view>
		<tabbar current="1" @getTabbarHeight="getTabbarHeight"></tabbar>
	</view>
</template>

<script>
	import tabbar from "@/components/tabbar.vue"
	import customHeader from "@/components/custom_header.vue"
	const app = getApp()
	export default {
		data() {
			return {
				imgUrl:app.globalData.baseImageUrl,
				showMask:false,
				refreshStatus:true,
				isPulling:false,
				pageName:"佣金",
				marginTop:app.globalData.marginTop,
				tabMargin:app.globalData.tabMargin,
				commiCardHeight:app.globalData.commiCardHeight,
				subTabHeight:app.globalData.subTabHeight,
				comValue:0,
				startTime:"",
				endTime:"",
				currentStatus:"",//"":全部，"running":进行中，"finished":已结束
				comRange:[{
					value:0,
					text:"不选择"
				}],
				tabs:[
					{
						value:"",
						text:"全部"
					},
					{
						value:"running",
						text:"进行中"
					},
					{
						value:"finished",
						text:"已结束"
					}
				],
				currentMainTab:0,
				scrollHeight:0,
				boxTop:0,
				headerHeight:app.globalData.topHeight,
				maskInnerTop:0,
				currentPage:1,
				info:{
					"balance": "",
					"expenditure": "",
					"income": "",
					"list": [],
					"pagination": {
						"page": 1,
						"pageCount": 1,
						"pageSize": 15,
						"totalCount": 1
					}
				},
				list:[],
				pagination:{},
				currentCompany:"",
				tabbarHeight:0
			}
		},
		components:{
			tabbar,customHeader
		},
		onLoad() {
			this.boxTop = this.marginTop + this.commiCardHeight + 20 + this.tabMargin
			this.scrollHeight = app.globalData.systemHeight - this.marginTop - this.commiCardHeight - this.tabbarHeight - 20
			if(this.isLogin()){
				this.getList()
				this.getCompanyList()
			}
		},
		// onPullDownRefresh() {
		// 	this.currentPage = 1
		// 	this.list = []
		// 	this.comRange = [{
		// 		value:0,
		// 		text:"不选择"
		// 	}]
		// 	this.getList()
		// 	this.getCompanyList()
		// },
		methods: {
			refreshData(){
				let _this = this
				if(!this.isPulling){
					this.isPulling = true
					this.refreshStatus = true
					this.currentPage = 1
					this.list = []
					this.comRange = [{
						value:0,
						text:"不选择"
					}]
					this.getList()
					this.getCompanyList()
					setTimeout(function() {
						_this.refreshStatus = false
						_this.isPulling = false
					}, 1000);
				}
			},
			getTabbarHeight(e){
				this.tabbarHeight = e
			},
			changeCom(e){
				console.log(e,"e")
				this.currentCompany = this.comRange[this.comValue].text == "不选择"?"":this.comRange[this.comValue].text
				console.log(this.currentCompany,'changecom')
			},
			changeStartTime(e){
				
			},
			changeEndTime(e){
				
			},
			changeStatus(item){
				this.currentStatus = item.value
			},
			getList(){
				let url = "/broker/mini/project-commissions?page="+this.currentPage+"&company_name="+this.currentCompany+"&project_status="+this.currentStatus+"&create_time_start="+this.startTime+"&create_time_end="+this.endTime;
				console.log(url)
				this.$request(url).then(res => {
					if(res.code == 0){
						this.info = res.data
						this.list = this.list.concat(res.data.list)
						this.pagination = res.data.pagination
					}else if(res.code != 2000100){
						uni.showModal({
							title:res.msg
						})
					}
				})
			},
			clear(){
				this.startTime = ""
				this.endTime = ""
				this.currentStatus = ""
				this.currentCompany = ""
				this.comValue = 0
				this.list = []
				this.currentPage = 1
				this.close()
				this.getList()
			},
			toDetail(id){
				uni.navigateTo({
					url:"/subpkg/commission_detail/commission_detail?id="+id
				})
			},
			openMask(type){
				this.showMask = true
				console.log(this.comRange,this.currentCompany)
				for(let i=0;i<this.comRange.length;i++){
					if(this.comRange[i].text == this.currentCompany){
						this.comValue = i
					}
				}
			},
			close(){
				this.showMask = false
			},
			searchList(){
				this.list = []
				this.currentPage = 1
				this.getList()
				this.close()
			},
			getCompanyList(){
				this.$request("/broker/mini/project-commissions/project-creator-companies").then(res => {
					if(res.code == 0){
						let len = res.data.length
						console.log(len,"len")
						for(let i=0;i<len;i++){
							let obj = {
								value:i+1,
								text:res.data[i]
							}
							this.comRange.push(JSON.parse(JSON.stringify(obj)))
						}
					}else if(res.code != 2000100){
						uni.showModal({
							title:res.msg
						})
					}
				})
			},
			lower(){
				if(this.list.length < this.pagination.totalCount){
					this.currentPage++
					this.getList()
				}else{
					if(this.list.length >= this.pagination.totalCount){
						uni.showToast({
							title:"已经到底啦~",
							icon:"none",
							duration:2000
						})
					}
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
::v-deep{
	.uni-select{
		border:none;
		background: #fff;
		height:88rpx;
		font-size: 28rpx;
		border-radius: 8rpx;
		padding:0 40rpx;
	}
	.uni-icons{
		display: none;
	}
	.uni-select__input-placeholder{
		font-size: 28rpx;
	}
	.uni-date-x--border {
	    height: 88rpx;
	    line-height: 88rpx;
	    padding: 0 40rpx;
	    background: #fff;
	    border-radius: 8rpx;
	    box-sizing: border-box;
		border:none;
	}
	.uni-datetime-picker--btn,.uni-calendar-item__weeks-box .uni-calendar-item--checked{
		background: #F7BC05;
	}
	.uni-datetime-picker-btn-text{
		color: #F7BC05;
	}
}
.top_area{
	width:calc(100% - 96rpx);
	position: fixed;
	left:48rpx;
	.account{
		position: relative;
		image{
			width: 100%;
			will-change: transform;
		}
		.top_line{
			width: 75%;
			position: absolute;
			top:25%;
			left:50%;
			transform: translateX(-50%);
			image{
				width:32rpx;
				height:32rpx;
				margin-right: 10rpx;
			}
			text{
				font-size: 32rpx;
				color:#333;
				font-weight: bold;
			}
			.bold{
				font-size: 52rpx;
				color:#fff;
				font-family: Arial, Arial;
				font-weight: 900;
				text-shadow: 0px 1px 2px #000000;
				margin-right: 8rpx;
			}
			.text{
				font-size: 24rpx;
				font-family: PingFangSC, PingFang SC;
				font-weight: 400;
				color: #FFFFFF;
			}
		}
		.bot_line{
			position: absolute;
			width: 75%;
			bottom:4%;
			left:50%;
			transform: translateX(-50%);
			.text{
				color:#A8A8A8;
				font-size: 24rpx;
				text-align: center;
			}
			.tit{
				font-size: 28rpx;
				color:#333;
				text-align: center;
				margin-bottom: 8rpx;
			}
			.yellow{
				font-weight: 900;
				font-size: 36rpx;
				color: #F3A005;
				text-align: center;
				font-style: normal;
				margin-right: 8rpx;
			}
		}
	}
	.sele_box{
		// margin:0 20rpx 20rpx 0;
		.text{
			font-size: 28rpx;
			color:#333;
		}
		image{
			width:26rpx;
			height:26rpx;
			margin-left: 8rpx;
		}
	}
}
.mask{
	.inner{
		width:80%;
		height:100%;
		position: absolute;
		top:0;
		right:0;
		background: #fff;
		.title{
			padding:0 60rpx;
			.text{
				font-size: 30rpx;
				color:#333;
				font-weight: bold;
			}
			image{
				width:44rpx;
				height:44rpx;
			}
		}
		.btn_wrap{
			padding:0 60rpx;
			margin-top:80rpx;
			.btn{
				// height:96rpx;
				// line-height: 96rpx;
				text-align: center;
				background: url($back-ground-url+"/broker/ic_btn_fill.png") no-repeat;
				background-size:100% 100%;
				color:#fff;
				font-weight: bold;
				font-size: 30rpx;
				border-radius: 16rpx;
				width:48%;
				// box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.15);
			}
		}
		.sele_line{
			.tit{
				margin-top:50rpx;
				padding:10rpx 60rpx 10rpx 80rpx;
				background: #fff;
				position: relative;
				color:#5A5A5A;
				font-size:28rpx;
				box-sizing: border-box;
				&:before{
					content:"";
					width:4px;
					height:14px;
					background: #F7BC05;
					border-radius: 1px;
					position: absolute;
					top:50%;
					left:60rpx;
					transform: translateY(-50%);
				}
			}
			.sele_area{
				padding:16rpx 60rpx;
				box-sizing: border-box;
				background: #F9F9F9;
				position: relative;
				&>image{
					width:24rpx;
					height:16rpx;
					position: absolute;
					top:50%;
					transform: translateY(-50%);
					right:100rpx;
				}
				.checkbox{
					height:88rpx;
					line-height: 88rpx;
					background: #fff;
					border-radius: 8rpx;
					padding:0 40rpx;
					.check{
						image{
							position: relative;
							width: 40rpx;
							height:40rpx;
							margin-right: 8rpx;
						}
						.text{
							font-size: 28rpx;
						}
					}
				}
				.link{
					text-align: center;
					font-size:28rpx;
					color:#A8A8A8;
					margin:8rpx 0;
				}
				.time{
					position: relative;
					image{
						width:24rpx;
						height:16rpx;
						position: absolute;
						top:50%;
						transform: translateY(-50%);
						right:40rpx;
					}
				}
			}
		}
	}
}
.box{
	position: fixed;
	width: calc(100% - 96rpx);
	left:48rpx;
	z-index:3;
	border-top-left-radius: 16rpx;
	border-top-right-radius: 16rpx;
	overflow: hidden;
	.item{
		padding:40rpx;
		border-radius: 16rpx;
		background: #fff;
		box-shadow: 0 2rpx 6rpx 0 rgba(0, 0, 0, 0.15);
		margin-bottom: 20rpx;
		.top{
			.title{
				font-size: 34rpx;
				padding-left: 22rpx;
				position: relative;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
				&:before{
					content:"";
					width:8rpx;
					height:36rpx;
					background: #F7BC05;
					position: absolute;
					top:50%;
					left:0;
					transform: translateY(-50%);
				}
			}
			.invite{
				height:60rpx;
				width:200rpx;
				padding:0 8rpx 0 16rpx;
				box-sizing: border-box;
				background: rgba(66, 132, 244, 0.1);
				border-radius: 10rpx;
				.tit{
					font-size: 24rpx;
					color:rgba(90, 90, 90, 1);
				}
				.count{
					width:48rpx;
					height:48rpx;
					line-height: 48rpx;
					border-radius: 14rpx;
					background: #3780FF;
					color:#fff;
					font-size: 30rpx;
					font-weight: bold;
					text-align: center;
				}
			}
		}
		.middle{
			font-size: 24rpx;
			margin:34rpx 0 0 0;
			padding:0 22rpx;
			.tit{
				color:#5A5A5A;
			}
			.count{
				font-size: 24rpx;
				color:#A8A8A8;
			}
			.mark{
				font-size: 36rpx;
				color:#333;
				margin-right: 8rpx;
			}
			.yellow{
				color:#F7BC05;
			}
		}
		.bottom{
			height:60rpx;
			border-radius: 6rpx;
			line-height: 60rpx;
			padding:0 22rpx;
			background: #F6F6F6;
			font-size: 28rpx;
			color:#686868;
		}
	}
}
</style>
